"use client";

import React, { useState } from "react";
import TacticalDashboard from "../src/components/TacticalDashboard";
import { Dashboard } from "../src/components/Dashboard";
import { DashboardPage } from "../src/components/DashboardPage";

export default function HomePage() {
  const [view, setView] = useState<'option1' | 'option2' | 'current'>('option1');

  return (
    <div className="relative h-screen w-screen">
      {/* View Selector - Top Center */}
      <div className="absolute top-4 left-1/2 -translate-x-1/2 z-50 flex gap-2 bg-black/90 backdrop-blur-md border border-white/20 rounded-full p-2 shadow-2xl">
        <button
          onClick={() => setView('option1')}
          className={`px-6 py-2 rounded-full text-xs font-bold uppercase tracking-wider transition-all ${view === 'option1'
              ? 'bg-cyan-500 text-black shadow-[0_0_20px_rgba(34,211,238,0.5)]'
              : 'text-zinc-400 hover:text-white hover:bg-white/10'
            }`}
        >
          Option 1: DashboardPage
        </button>
        <button
          onClick={() => setView('option2')}
          className={`px-6 py-2 rounded-full text-xs font-bold uppercase tracking-wider transition-all ${view === 'option2'
              ? 'bg-emerald-500 text-black shadow-[0_0_20px_rgba(16,185,129,0.5)]'
              : 'text-zinc-400 hover:text-white hover:bg-white/10'
            }`}
        >
          Option 2: Dashboard
        </button>
        <button
          onClick={() => setView('current')}
          className={`px-6 py-2 rounded-full text-xs font-bold uppercase tracking-wider transition-all ${view === 'current'
              ? 'bg-red-500 text-black shadow-[0_0_20px_rgba(239,68,68,0.5)]'
              : 'text-zinc-400 hover:text-white hover:bg-white/10'
            }`}
        >
          Current: TacticalDashboard
        </button>
      </div>

      {/* Render Selected View */}
      {view === 'option1' && <DashboardPage />}
      {view === 'option2' && <Dashboard />}
      {view === 'current' && <TacticalDashboard />}
    </div>
  );
}
